.card-items {
  border: 1px solid rgb(218, 209, 198);
  background-color: #fff;
  padding: .5em;
  margin: 2em;
  -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);

}

.prop-buttons {
  width: 60%;
  height: auto;
  background-position: center center;
  background-repeat: no-repeat;
}

.style-text {
  color: #000;
  font-weight: 900;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif, Cochin, Georgia, Times, 'Times New Roman';
  font-size: 18px;
  letter-spacing: 3px;
  padding: 1em;
}

.title-text {
  text-align: center;
  background-color: aquamarine;
  padding: 1em 0;
  border: 1px solid #fff;
  opacity: .6;
}


